import React, { useCallback, useEffect, useReducer, useRef, useState } from 'react';

import styled, { css } from 'styled-components';

import OpenFin from '@openfin/core';

import { Box, DropdownMenu, Heading, Icon, MenuOption } from '@openfin/ui-library';

import { INTENTS_RESOLVER_MODAL_TIMEOUT_MS } from '@common/utils/modal-constants';

import { ButtonGroup } from '@enterprise/components/ModalContent/ButtonGroup';

import { FDC3Context, IntentResolverApp, IntentResolverIntent, IntentResolverResponse } from '@client-platform/shapes';
import { MAC_WINDOW_BORDER_RADIUS } from '@common/mixins/padding-border';
import { useIsWindows } from '@enterprise/hooks/useIsWindows';

const INTENTS_MODAL_DROP_DOWN_POSITION = 'above';

export type IntentsResolverModalActionHandler = (action: IntentResolverResponse) => void;

export type IntentsResolverModalProps = {
    title: string;
    apps?: IntentResolverApp[];
    intents?: IntentResolverIntent[];
    context?: FDC3Context;
    timeoutMs?: number;
    preselected?: {
        appId?: string;
        intentName?: string;
        instanceId?: string;
    };
    onAction: IntentsResolverModalActionHandler;
    invocationId?: string;
};

const NEW_TAB_OPTION = {
    title: 'New Tab',
    value: ''
} satisfies MenuOption;

const AppList = ({
    options,
    onAppSelected,
    selectedApp
}: {
    options: InstanceMenuOption<IntentResolverApp>[];
    onAppSelected: (app: InstanceMenuOption<IntentResolverApp>) => void;
    selectedApp: InstanceMenuOption<IntentResolverApp>;
}) => {
    return (
        <>
            {options.map((option) => (
                <AppCard
                    key={option.value.appId}
                    selected={selectedApp.value.appId === option.value.appId}
                    onClick={() => onAppSelected(option)}
                >
                    <AppIcon>
                        {option.value.icon ? (
                            <img src={option.value.icon} alt={option.value.icon} />
                        ) : (
                            // !Somehow Image from nextjs breaks the app to the point that I can't even start the EB.
                            // <Image alt={app.name} width={24} height={24} src={app.icon} />
                            <DefaultIcon icon="StackIcon" />
                        )}
                    </AppIcon>
                    <AppInfo>
                        <AppName>{option.value.name}</AppName>
                    </AppInfo>
                </AppCard>
            ))}
        </>
    );
};

type InstanceMenuOption<T> = {
    title: string;
    value: T;
};

type MenuState<T> = {
    selected?: InstanceMenuOption<T>;
    options: InstanceMenuOption<T>[];
};

type SetSelectedOptionAction<T> = {
    type: 'setSelectedOption';
    selected: InstanceMenuOption<T>;
};

type SetMenuOptions<T> = {
    type: 'setMenuOptions';
} & MenuState<T>;

function menuStateReducer<T>(
    state: MenuState<T>,
    action: SetSelectedOptionAction<T> | SetMenuOptions<T>
): MenuState<T> {
    switch (action.type) {
        case 'setMenuOptions':
            return {
                selected: action.selected || action.options[0],
                options: action.options
            };
        case 'setSelectedOption':
            return {
                ...state,
                selected: action.selected
            };
        default:
            return state;
    }
}

const IntentsResolver: React.FC<Omit<IntentsResolverModalProps, 'title'>> = ({
    apps,
    intents,
    timeoutMs = INTENTS_RESOLVER_MODAL_TIMEOUT_MS,
    preselected,
    onAction,
    invocationId
}) => {
    const [instanceMenu, updateInstanceMenu] = useReducer(menuStateReducer<string>, { options: [NEW_TAB_OPTION] });
    const [appMenu, updateAppMenu] = useReducer(menuStateReducer<IntentResolverApp>, { options: [] });
    const [intentMenu, updateIntentMenu] = useReducer(menuStateReducer<IntentResolverIntent>, { options: [] });

    const [timeRemaining, setTimeRemaining] = useState<number>(Math.floor(timeoutMs / 1000));

    useEffect(() => {
        updateIntentMenu({
            type: 'setMenuOptions',
            options: (intents || []).map((intent) => ({
                title: intent.displayName || intent.name,
                value: intent
            }))
        });
    }, [intents]);

    useEffect(() => {
        if (intentMenu.options.length > 0) {
            const selectedIntent = intentMenu.selected?.value;
            updateAppMenu({
                type: 'setMenuOptions',
                options: selectedIntent.apps.map((app) => ({
                    title: app.name,
                    value: app
                }))
            });
        } else {
            updateAppMenu({
                type: 'setMenuOptions',
                options: apps.map((app) => ({
                    title: app.name,
                    value: app
                }))
            });
        }
    }, [apps, intentMenu.selected, intentMenu.options]);

    useEffect(() => {
        const instances = appMenu.selected?.value.instances || [];
        const instanceMenuOptions: InstanceMenuOption<string>[] = [
            NEW_TAB_OPTION,
            ...instances.map((instance) => ({
                title: instance.instanceName,
                value: instance.instanceId,
                iconUrl: instance.icon
            }))
        ];

        const instanceId = preselected?.instanceId || NEW_TAB_OPTION.value;

        updateInstanceMenu({
            type: 'setMenuOptions',
            options: instanceMenuOptions,
            selected:
                // Default to the first option (new tab) if no preselection provided
                instanceMenuOptions.find((option) => option.value === instanceId) || instanceMenuOptions[0]
        });
    }, [appMenu, preselected?.instanceId]);

    useEffect(() => {
        console.info(`Intent Picker Invocation ID changed to ${invocationId}. Resetting timer.`);
        setTimeRemaining(Math.floor(timeoutMs / 1000));
    }, [invocationId, timeoutMs]);

    useEffect(() => {
        if (timeRemaining > 0) {
            const timer = setTimeout(() => {
                setTimeRemaining(timeRemaining - 1);
            }, 1000);
            return () => clearTimeout(timer);
        } else if (timeRemaining <= 0) {
            // Timeout reached - send cancel response
            onAction({ type: 'cancel' });
        }
    }, [timeRemaining, onAction]);

    const dispatchOpenAction = useCallback(() => {
        const responseData: Extract<IntentResolverResponse, { type: 'open' }>['data'] = {
            appId: appMenu.selected.value.appId,
            instanceId: instanceMenu.selected?.value || '',
            intentName: intentMenu.selected?.value.name,
            rememberSelection: false
        };

        onAction({ type: 'open', data: responseData });
    }, [onAction, appMenu, instanceMenu, intentMenu]);

    // Handle keyboard shortcuts (Enter key for Open button)
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.key === 'Enter' && appMenu.selected) {
                dispatchOpenAction();
            }
        };

        document.addEventListener('keydown', handleKeyDown);

        return () => document.removeEventListener('keydown', handleKeyDown);
    }, [dispatchOpenAction, appMenu.selected]);

    const handleButtonAction = (action: { name: IntentResolverResponse['type'] }) => {
        if (action.name === 'open' && appMenu.selected.value.appId) {
            dispatchOpenAction();
        } else {
            onAction({ type: 'cancel' });
        }
    };

    const maxHeightForThreeOptions = '210px'; // spec is 208px but adding 2px for the border top/bottom to avoid scrolling

    return (
        <>
            <AppsSection>
                <AppList
                    onAppSelected={(app) => {
                        updateAppMenu({ type: 'setSelectedOption', selected: app });
                    }}
                    selectedApp={appMenu.selected}
                    options={appMenu.options}
                />
            </AppsSection>

            <DropdownSection>
                <DropdownMenu
                    label="Open in:"
                    onChange={(option: InstanceMenuOption<string>) => {
                        updateInstanceMenu({ type: 'setSelectedOption', selected: option });
                    }}
                    selected={instanceMenu.selected}
                    options={[instanceMenu.options]}
                    menuPosition={INTENTS_MODAL_DROP_DOWN_POSITION}
                    maxHeight={maxHeightForThreeOptions}
                    // All dropdowns render above
                    // MaxHeight set to show 3 options before scrolling
                />
            </DropdownSection>

            {intentMenu.options.length > 0 && (
                <>
                    <DropdownSeparator />
                    <DropdownSection>
                        <DropdownMenu
                            label="What would you like to do?"
                            onChange={(e: InstanceMenuOption<IntentResolverIntent>) => {
                                // setSelectedIntentName(String(e.value));
                                updateIntentMenu({
                                    type: 'setSelectedOption',
                                    selected: e
                                });
                            }}
                            selected={intentMenu.selected}
                            options={[intentMenu.options]}
                            menuPosition={INTENTS_MODAL_DROP_DOWN_POSITION}
                            maxHeight={maxHeightForThreeOptions}
                        />
                    </DropdownSection>
                </>
            )}

            <DropdownSeparator className="without-margin" />

            <BottomSection>
                <ButtonsForm
                    onSubmit={(e) => {
                        e.preventDefault();
                        if (appMenu.selected) {
                            handleButtonAction({ name: 'open' });
                        }
                    }}
                >
                    <ButtonGroup
                        align="left"
                        buttons={[
                            { label: 'Cancel', type: 'secondary', id: 'cancel' as IntentResolverResponse['type'] }
                        ]}
                        onAction={handleButtonAction}
                    />
                    <ButtonGroup
                        align="right"
                        buttons={[
                            {
                                label: 'Open',
                                type: 'primary',
                                id: 'open' as IntentResolverResponse['type']
                            }
                        ]}
                        onAction={handleButtonAction}
                        hasError={!appMenu.selected?.value.appId}
                    />
                </ButtonsForm>
            </BottomSection>
        </>
    );
};

/**
 * Main Container for the Intents Resolver - this avoids us needing to have branching returns in
 * IntentsResolverApp and keeps it pure.
 *
 * This also isolates the ResizeObserver logic from main IntentsResolver component, again just to improve readability
 * and maintainability.
 */
export const IntentsResolverModal: React.FC<IntentsResolverModalProps> = ({
    title,
    apps = [],
    intents,
    timeoutMs = INTENTS_RESOLVER_MODAL_TIMEOUT_MS,
    preselected,
    onAction,
    invocationId
}) => {
    const shouldShowError = apps.length === 0 && intents?.length === 0;
    const isWindows = useIsWindows();

    const modalContainerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const observer = new ResizeObserver((entries) => {
            for (const entry of entries) {
                if (entry.target === modalContainerRef.current) {
                    if (fin.me.isWindow) {
                        (fin.me as OpenFin.Window).setBounds({ height: modalContainerRef.current.offsetHeight });
                    }
                    return;
                }
            }
        });

        if (modalContainerRef.current) {
            observer.observe(modalContainerRef.current);
        }

        return () => {
            observer.disconnect();
        };
    }, [invocationId]);

    return (
        <MainContainer isWindows={isWindows} ref={modalContainerRef}>
            <HeaderSection>
                <Header>{title}</Header>
                <CloseButton
                    tabIndex={0}
                    aria-label="Close intent picker dialog"
                    onClick={() => onAction({ type: 'cancel' })}
                />
            </HeaderSection>
            {shouldShowError && (
                <div style={{ padding: '20px', textAlign: 'center' }}>
                    <h3>No applications or intents available</h3>
                    <p>No applications or intents are available to handle this request.</p>
                </div>
            )}

            {!shouldShowError && (
                <IntentsResolver
                    apps={apps}
                    intents={intents}
                    timeoutMs={timeoutMs}
                    preselected={preselected}
                    onAction={onAction}
                    invocationId={invocationId}
                />
            )}
        </MainContainer>
    );
};

