import React, { useEffect, useMemo, useState } from 'react';

import { ErrorBoundary } from 'next/dist/client/components/error-boundary';

import { OpenFin } from '@openfin/core';

import { isFin } from '@common/utils/env';
import { getUrlParams, Router, withRouter } from '@common/utils/router';

import { IntentsResolverModal } from '@enterprise/components/ModalContent/IntentsResolverModal';

import { IntentResolverResponse, ShowIntentsResolverModalRequestInternal } from '@client-platform/shapes';
import { BrowserTypeContext } from '@common/hooks/context';

type IntentsResolverModalPageProps = {
    router: Router;
};

const ErrorFallback = ({ error }: { error: Error & { digest?: string } }) => {
    useEffect(() => {
        if (isFin) {
            dispatchPopupResult({
                type: 'error',
                error: { message: error.message, name: error.name, stack: error.stack, cause: error.cause },
                modalIdentity: fin.me.identity
            });
        }
    }, [error]);

    return (
        <div>
            <h2>Client-side exception has occurred</h2>
            <p>{error.message}</p>
        </div>
    );
};

const dispatchPopupResult = (result: IntentResolverResponse) =>
    isFin && (fin as OpenFin.Fin<'window'>).me.dispatchPopupResult(result);

const IntentsResolverModalPage: React.FC<IntentsResolverModalPageProps> = () => {
    const [urlHash, setUrlHash] = useState(typeof window !== 'undefined' ? window.location.hash : '');
    const urlParams = useMemo(() => getUrlParams(urlHash), [urlHash]);

    useEffect(() => {
        const onHashChange = () => setUrlHash(window.location.hash);

        window.addEventListener('hashchange', onHashChange);
        return () => window.removeEventListener('hashchange', onHashChange);
    }, []);

    // Parse the URL parameters into the expected format
    const parsedContent: ShowIntentsResolverModalRequestInternal | null = extractModalContent(urlParams);

    const handleAction = async (action: IntentResolverResponse) => {
        try {
            await dispatchPopupResult(action);
        } catch (error) {
            console.error('Error sending intent resolver response:', error);
            await dispatchPopupResult({ type: 'cancel' });
        }
    };

    if (parsedContent) {
        return (
            <BrowserTypeContext.Provider value={{ isEnterprise: true }}>
                <ErrorBoundary errorComponent={ErrorFallback}>
                    <IntentsResolverModal
                        title={parsedContent.title}
                        apps={'apps' in parsedContent ? parsedContent.apps : undefined}
                        intents={'intents' in parsedContent ? parsedContent.intents : undefined}
                        context={parsedContent.context}
                        timeoutMs={parsedContent.timeoutMs}
                        preselected={parsedContent.preselected}
                        onAction={handleAction}
                        invocationId={urlParams.invocationId}
                    />
                </ErrorBoundary>
            </BrowserTypeContext.Provider>
        );
    }

    // Fallback UI for when content cannot be parsed
    return (
        <div style={{ padding: '20px', textAlign: 'center' }}>
            <h3>Unable to load modal content</h3>
            <p>The modal content could not be parsed. Please check the console for details.</p>
            <button onClick={() => handleAction({ type: 'cancel' })}>Close</button>
        </div>
    );
};

function extractModalContent(urlParams: { [k: string]: string }): ShowIntentsResolverModalRequestInternal | null {
    try {
        if (!urlParams.title || !(urlParams.apps || urlParams.intents)) {
            console.warn('Missing required parameters (title or apps or intents)');
            return null;
        }

        const modalData: ShowIntentsResolverModalRequestInternal = {
            title: urlParams.title,
            identity: { name: urlParams.parentName || '', uuid: '' }, // Will be filled by platform
            ...(urlParams.apps && { apps: JSON.parse(urlParams.apps) }),
            ...(urlParams.intents && { intents: JSON.parse(urlParams.intents) }),
            ...(urlParams.context && { context: JSON.parse(urlParams.context) }),
            ...(urlParams.timeoutMs && { timeoutMs: parseInt(urlParams.timeoutMs, 10) }),
            ...(urlParams.preselected && { preselected: JSON.parse(urlParams.preselected) })
        };

        return modalData;
    } catch (error) {
        console.error('Failed to parse URL parameters:', error);
        return null;
    }
}

export default withRouter(IntentsResolverModalPage);
